@import "../variables";

.header {
	border-bottom: 1px solid $gray-line;
	padding: 40px 0;
	&__cart {
		display: flex;
		align-items: center;
	}
	.container {
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: 20px;
		@media (max-width: 660px) {
			flex-direction: column;
			// align-items: start;
			gap: 30px;
			& a {
				width: 100%;
				justify-content: center;
			}
		}
	}

	&__logo {
		display: flex;

		img {
			margin-right: 15px;
		}

		h1 {
			color: #181818;
			font-size: 24px;
			letter-spacing: 1%;
			text-transform: uppercase;
			font-weight: 800;
		}

		p {
			color: #7b7b7b;
		}
	}
}
